<template>
    <a-layout-header class="header">
        <div class="logo" >
            <a href="/" style="color: white">
                wiki
            </a>
        </div>
        <a-menu
                theme="dark"
                mode="horizontal"
                :style="{ lineHeight: '64px'}"
                class="re_menu"
        >
            <a-menu-item key="Home">
                <router-link to="/"></router-link>
            </a-menu-item>
            <a-menu-item key="AdminUser" :style="user.id? {} : {display:'none'}">
                <router-link to="/admin/user"></router-link>
            </a-menu-item>
            <a-menu-item key="AdminEbook" :style="user.id? {} : {display:'none'}">
                <router-link to="/admin/ebook"></router-link>
            </a-menu-item>
            <a-menu-item key="AdminCategory" :style="user.id? {} : {display:'none'}">
                <router-link to="/admin/category"></router-link>
            </a-menu-item>
            <a-menu-item key="About">
                <router-link to="/about"></router-link>
            </a-menu-item>
            <a-popconfirm
                title=""
                ok-text=""
                cancel-text=""
                @confirm="logout()"
            >
                <a class="login-menu" v-show="user.id">
                    <span></span>
                </a>
            </a-popconfirm>
            <a class="login-menu" v-show="user.id">
                <span>: {{user.name}}</span>
            </a>
            <a class="login-menu" v-show="!user.id" @click="showLoginModal">
                <span></span>
            </a>
        </a-menu>
        <a-modal
                title=""
                v-model:visible="loginModalVisible"
                :confirm-loading="loginModalLoading"
                @ok="login"
                okText=""
                cancelText=""
        >
            <a-form :model="loginUser" :label-col="{ span: 4 }" :wrapper-col="{ span: 19 }">
                <a-form-item label="">
                    <a-input v-model:value="loginName" placeholder="" />
                </a-form-item>
                <a-form-item label="">
                    <a-input v-model:value="password" type="password" placeholder=""/>
                </a-form-item>
            </a-form>
        </a-modal>
    </a-layout-header>
</template>

<script lang="ts">
    import {defineComponent, ref, computed, h, onMounted} from 'vue';
    import axios from 'axios';
    import {message, notification} from "ant-design-vue";
    import store from "@/store";
    import SmileOutlined from "@ant-design/icons-vue/SmileOutlined";
    import {Tool} from "@/util/tool";

    declare let hexMd5: any;
    declare let KEY: any;

    export default defineComponent({
        name: 'the-header',
        setup () {
            // 
            const user = computed(() => store.state.user);

            // the-footerWebSocket
            let websocket: any;
            let token: any;

            // 
            const loginUser = ref({
               loginName: "",
                password: ""
            });
            const loginName = ref("");
            const password = ref("");
            const loginModalVisible = ref(false);
            const loginModalLoading = ref(false);
            const showLoginModal = () => {
                loginModalVisible.value = true;
            }

            // 
            const login = () => {
                console.log("");
                loginModalLoading.value = true;
                loginUser.value.loginName = loginName.value;
                loginUser.value.password = hexMd5(password.value + KEY);
                axios.post('/user/login', loginUser.value).then((response) => {
                    loginModalLoading.value = false;
                    const data = response.data;
                    if (data.success) {
                        loginModalVisible.value = false;
                        message.success("!");
                        store.commit("setUser", data.content);
                    } else {
                        message.error(data.message);
                    }
                });
            };

            // 
            const logout = () => {
                console.log("");
                axios.get('/user/logout/' + user.value.token).then((response) => {
                    const data = response.data;
                    if (data.success) {
                        message.success("!");
                        store.commit("setUser", {});
                        loginName.value = "";
                        password.value = "";
                        //
                        location.reload();
                    } else {
                        message.error(data.message);
                    }
                });
            };

// ==========================================================
            // the-footerWebSocket
            const onOpen = () => {
                console.log('WebSocket,:', websocket.readyState);
            };
            const onMessage = (event: any) => {
                console.log('WebSocket: ', event.data);
                notification.open({
                    message: '',
                    description: event.data,
                    icon: h(SmileOutlined, { style: 'color: #108ee9' }),
                });
            };
            const onError = () => {
                console.log('WebSocket: ', websocket.readyState);
            };
            const onClose = () => {
                console.log('WebSocket: ', websocket.readyState);
            };
            const initWebSocket = () => {
                // 
                websocket.onopen = onOpen;
                // 
                websocket.onmessage = onMessage;
                // 
                websocket.onerror = onError;
                // 
                websocket.onclose = onClose;
            };

            onMounted(() => {
                // WebSocket
                if ('WebSocket' in window) {
                    token = Tool.uuid(10);
                    // :ws://127.0.0.1:8081/ws/xxx
                    websocket = new WebSocket(process.env.VUE_APP_WS_SERVER + '/ws/' + token);
                    initWebSocket();

                    // 
                    // websocket.close();
                } else {
                    alert('');
                }
            });


            return {
                loginModalVisible,
                loginModalLoading,
                showLoginModal,
                loginUser,
                login,
                user,
                logout,
                loginName,
                password
            }
        }
    });
</script>

<style>
    .logo {
        width: 180px;
        height: 64px;
        float: left;
        color: white;
        font-size: 18px;
    }
    .login-menu{
        float: right;
        color: white;
        padding-left: 10px;
    }
</style>